* {
  margin: 0;
  padding: 0;
  font-family: -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    "Open Sans",
    "Helvetica Neue",
    sans-serif;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
  /* overflow-y: scroll; */
}

.phones-catalog {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
}

.header-container {
  display: flex;
  flex: 0 0 auto;
  justify-content: space-between;
  width: 100%;
  height: 60px;
  align-items: center;
  /* padding: 0 5%; */
  margin-bottom: 15px;
  box-shadow: 0 0 10px #f0f0f0;
  /* background-color: rgb(235, 235, 235); */
  margin-bottom: 30px;
}

.header-logo > a > img {
  max-height: 50px;
  margin-left: 40px;
}

.header-logo:hover {
  transform: scale(1.02);
}

.nav-links {
}

.nav-items {
   display: flex;
}


.nav-items > a {
  text-decoration: none;
  color: #000000;
}

/* .nav-items > a:active {
  text-decoration: overline  ;
  color: #00ACDC;
} */


ul > .active:not(:last-child) {
  text-decoration: overline  ;
  color: #00ACDC;
}

.nav-item {
  margin-right: 25px;
  list-style-type: none;
  line-height: 38px;
  font-size: 18px;
  font-weight: 500;
  text-transform: uppercase;
  padding: 5px;
  /* background-color: #45b1a132;
  border: 1px solid #80808030;
  border-radius: 10px; */
}

.nav-item.cart-item:active {
  background-color: rgba(0, 172, 220, 0.122);
  border-radius: 6px;
  /* border: 1px solid blue; */
}

.cart-icon {
  width: 26px;
  padding-top: 5px;
  /* box-sizing: content-box; */
  opacity: 0.7;
}

.cart-quantity {
  font-size: 14px;
  color: #00ACDC;
}

.content-container {
  flex: 1 0 auto;
  width: 70%;
  text-align: center;
  width: 90%;
  margin: 10px auto 50px;
}

.search-panel {
width: 100%;
}

.pagination-box {
  display: flex;
  justify-content: space-between;
  width: 90%;
}

.pagination-panel {
  display: flex;
  list-style-type: none;
}

