.phones-item {
  /* max-width: 25%; */
  /* max-width: 21%; */
  /* min-width: 240px; */
  height: 380px;
  margin: 10px 5px;
  padding: 5px 15px 10px;
  background: #FFFFFF;
  border: 1px solid #F3F3F3;
  box-sizing: border-box;
  border-radius: 5px;
  box-shadow: 0 0 18px #f5f4f4;
  opacity: 0.92;
  transition: all .3s;

}

.phones-item:hover {
  /* transform: scale(1.0); */
  opacity: 1;
  box-shadow: 0 0 20px #e2e2e2;
  transition:all .3s;

}

.phones-item-info {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center
}

.phones-main-photo {
  width: 150px;
}

.phones-item-head > p {
  text-align: left;
  padding: 10px 0 0;
  font-size: 13px;
  line-height: 20px;
}

.phones-item-title {
  text-decoration: none;
  color: #000000;
}

.phones-item-title:hover {
  text-decoration: none;
  color: #00ACDC;
}

.add-btn {
  padding: 5px;
  width: 80%;
  background-color: #00ACDC;
  transition: color .3s,background-color .3s;

}

.add-btn:hover {
  background-color: #fff;
  color: #00ACDC;
  transition: color .3s,background-color .3s;

}

.pagination-box {
  display: flex;
  justify-content: space-between;
  margin: 25px auto 0;
}

.pagination-panel {
  display: flex;
  list-style-type: none;
}

.btn-pgn.selected {
  font-size: bold;
  color: #fff;
  background-color: rgb(9, 195, 247);
  transform: scale(1.05);
  opacity: 1;
  transition: all 0.2s;
  box-shadow: 0 0 8px rgb(114, 205, 228)
}

.total-range {
  padding-left: 40px;
}

input {
  width: 280px;
}

.btn-pgn {
  width: 36px;
  background-color: rgba(0, 172, 220, 0.782);

  opacity: 0.8;
  transition: all 0.2s;
}

.btn-pgn:hover {
  transform: scale(1.05);
  background-color: #b1cad17d;
  opacity: 1;
  transition: all 0.2s;
}

.btn-pgn:active {
  background-color: #73b9d0;
  color: #fff;
  transition: color 0.4s;
  transform: scale(1);
}

.per-page-select {
  margin-left: 20px;
  /* height: 39px; */
  /* border-radius: 10px; */
  /* background-color: #b28bd83d; */
}




.btn {
  font-family: "Lucida Sans",
    "Lucida Sans Regular",
    "Lucida Grande",
    "Lucida Sans Unicode",
    Geneva,
    Verdana,
    sans-serif ;
  padding: 5px 3px;
  margin: 2px;
  border: 1px solid #1a1f38;
  outline-style: none;
  border-radius: 5px;
  /* background-color: #00ACDC; */
  color: #fff;
  /* font-size: 16px; */
  font-weight: 600;
  cursor: pointer;
}

.footer-info {
  display: flex;
  flex: 0 0 auto;

  justify-content: space-around;
  align-items: center;
  height: 60px;
  background-color: silver;
  box-shadow: 0 0 20px #d8d8d8;
}


.footer-info > span > a {
  text-decoration: none;
  color: #696969b4;
}

.footer-info > span > a:hover {
  color: #2b2c2b;
}





/*--- HOME_PAGE ---*//*--- HOME PAGE ---*/

.welcome-container {
  max-width: max-content;
  margin: 100px auto;
  text-align: center;

}

.welcome-container > a {
  text-decoration: none;
  color: #000000a0;
  transition: all 1s;

}

.welcome-container > a:hover {
  color: #28a6dcf3;
  transition: 0.5 s;
}

.welcome-text {
  font-size: 40px;
  text-decoration: none;
}






/*--- PHONE-DETAILS ---*//*--- PHONE-DETAILS ---*/

.btn.btn-back {
  width: 48%;
  background-color: #ffffff21;
  color: #202020;
  /* border: none; */
}

.btn-back:hover {
  color: #204375;
  /* background-color: #faf5f5; */
  box-shadow: 1px 1px 5px #cccccc;
}

.btn.btn-buy {
  width: 50%;
  /* opacity: 0; */
  /* border:none;  */
  background-color: #ffffff30;
  color: #202020;
}

.btn-buy:hover {
  /* background-color: #2fff4e66; */
  color: #204375;
  box-shadow: 1px 1px 5px #cccccc;
}

.extra-details {
  display: flex;
  flex: 2 0 auto;
  width: 100%;
  margin: 20px 0;
}

.extra-details-title {
  font-size: 26px;
  font-weight: 700;
}

.extra-details-photo-selected {
  width: 350px;
  height: 350px;
  margin-right: 40px;
}

.selected-photo {
  height: 100%;
}

.extra-details-text {
  /* text-align: left; */
}

.extra-details-description {
  margin: 10px 0;
  text-align: left;
}

.extra-details-photos {
  display: flex;
  flex-wrap: wrap;
  margin: 15px 0;
  list-style-type: none;
  justify-content: flex-start;
}

.extra-details-photos-item {
  height: 80px;
  width: 80px;
  border: 1px solid gray;
  border-radius: 5px;
  padding: 5px;
  margin-right: 5px;
  opacity: 0.85;
  cursor: pointer;
}

.extra-details-photos-item:hover {
  opacity: 1;
}

.extra-details-photos-item-cart-add {
  border: 1px solid #940a0a;
}

.extra-details-photos-item-cart-add:hover {
  border: 1px solid #007fce;
}

/*--- PHONE-MORE-DETAILS ---*//*--- PHONE-MORE-DETAILS ---*/

.extra-details-more-details {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-size: 13px;
}

.more-info {
  width: 24%;
  margin-top: 35px;
  text-align: left;
}

/*--- FOOTER ---*//*--- FOOTER ---*/

/* .footer-container {
  display: flex;
  flex: 0 0 auto;
  height: 80px;
  width: 100%;
  align-items: center;
  background-color: #47474750;
} */

.footer-info {
  font-size: 20px;
  display: flex;
  flex: 0 0 auto;
  justify-content: space-evenly;
  width: 100%;
  text-align: center;
  color: #757575;
  /* background: linear-gradient(to top, #DFA376 -100%, white 70%); */
  background-color:#00ACDC;
}

.footer-info > span > a {
  text-decoration: none;
  color: #696969b4;
}

.footer-info > span > a:hover {
  color: #ffffff;
}

/*--- LOADER ---*//*--- LOADER ---*/

.sweet-loading {
  width: 100px;
  margin: 100px auto;
}






/*--- CART ---*//*--- CART ---*/

.cart-empty {
  max-width: max-content;
  margin: 100px auto;
  text-align: center;

}

.cart-empty > a {
  text-decoration: none;
  color: #000000a0;
  transition: all 1s;

}

.cart-empty > a:hover {
  color: #239cd0;
  transition: 0.5 s;
}

.cart-empty-link {
  display: block;
  width: max-content;
  margin: 30px auto;
  font-size: 45px;
}

.cart-empty-link:hover {
  transform: scale(1.1);
}

.cart-empty {
  width: 70%;
  margin: 100px auto;
  font-size: 32px;
  text-decoration: none;
}

.order-container {
  display: flex;
  flex-wrap: wrap;
}

.order-box {
  width: 100%;
}

.order-box>h3 {
  text-align: left;
}

.order-content {
  border-radius: 15px;
  margin-bottom: 25px;
}

.order-list {
  list-style-type: none;
}

.order-item {
  text-align: center;
  width: 100%;
  padding: 0 10px;
  border: 1px solid #7373756f;
  border-radius: 20px;
  box-shadow: 2px 4px 8px #8080803e;
  opacity: 0.85;
}

.linked-order-photo {
  height: 80px;
  cursor: pointer;
  opacity: 0.9;
}

.linked-order-photo:hover {
  opacity: 1;
  transform: scale(1.05);
  transition: transform 0.3s;
}

.order-quantity {
  font-size: 22px;
  font-weight: 500;
}

th {
  width: 32%;
}

.order-control-block {
  display: flex;
  justify-content: flex-end;
}

.btn-control {
  background-color: #fff;
  border: none;
  width: 29px;
  height: 31px;
  opacity: 0.6;
  transition: all 0.2s;
}

.btn-control:hover {
  opacity: 1;
  transform: scale(1.02);
  transition: all 0.2s;
}

.btn-control:active {
  color: #fff;
  transform: scale(0.9);
}

.control-btn-icons {
  width: 20px;
  height: 20px;
  user-select: none;
}

.order-btn-block {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  width: 100%;
}

.order-total {
  color: #272727;
  line-height: 36px;
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
}

.btn-add-more {
  width: 100%;
  background-color: #1effb809;
  text-transform: uppercase;
  opacity: 0.5;
  transition: 0.3s;
}

.btn-add-more:hover {
  color: #000;
  background-color: #1effb809;
  opacity: 1;
}

.btn-add-more:active {
  color: #000;
  background-color: #1effb816;
  transform: scale(0.99);
}

.btn-pay {
  transition: 0.1s;
}

.btn-pay:active {
  transform: scale(0.99);
  color: #1606c6;
  background-color: #6cec859a;
  transition: 0.1s;
}

.hidden {
  display: none;
}
